<template>
    <div>
        <v-toolbar app>
            <v-toolbar-side-icon class="menu-button" @click="drawer =  !drawer"></v-toolbar-side-icon>
            <v-toolbar-title><img src="/logo.png" alt="logo" width="24px" height="24px" style="border-radius: 50%; margin-right: 5px; vertical-align: middle;"> <span class="headline text-uppercase">Success</span></v-toolbar-title>
            <v-spacer></v-spacer>  
            <v-btn flat v-for="(link, i) in links" :key="i" :class="link.class + 1" class="hidden-sm-and-down link">
                {{link.link }} <v-icon class="ml-1" small>{{link.icon}}</v-icon>
            </v-btn>
        </v-toolbar>

        <!--Navigation drawer-->
        <v-navigation-drawer app v-model="drawer">
            <v-toolbar flat extended>
                <v-card-title class="mt-5">
                    <img src="/logo.png" alt="logo" width="30px" height="30px" style="border-radius: 50%; margin-right: 5px; vertical-align: middle;">
                    <span class="font-weight-bold subheading grey--text text--darken-4">Success With Kelvin</span>
                </v-card-title>
            </v-toolbar>

            <v-divider></v-divider>

            <v-list dense class="pt-0">
                <v-card v-for="(item, index) in links" :key="index">
                    <v-card-text class="subheading font-weight-bold grey--text text--darken-3 nav">
                        <v-avatar size="40" color="grey darken-4" class="mr-3">
                            <v-icon>{{ item.icon }}</v-icon>
                        </v-avatar>
                        {{ item.link }} 
                    </v-card-text>
                </v-card>
                <v-divider></v-divider>
                    <v-expansion-panel focusable>
                        <v-expansion-panel-content>
                            <div slot="header">Categories</div>
                            <v-card flat v-for="(item, index) in items" :key="index">
                                <a class="link-ex" :href="item.route">
                                    <v-card-text class="subheading link-ex">
                                       {{item.text}}
                                    </v-card-text>
                                </a>
                            </v-card>
                        </v-expansion-panel-content>
                    </v-expansion-panel>
            </v-list>
        </v-navigation-drawer>
    </div>
</template>

<script>
export default {
    data() {
        return {
            drawer: false,
            links: [
                {link: "Home", icon:"fas fa-home"},
                {link: "About", icon:"fas fa-about"},
                {link: "Log In", icon:"fas fa-sign-in-alt", class: "log"},
                {link: "Sign Up", icon:"fas fa-user-plus", class: "sign"},
            ],
            drawer_list: [
                {link: "Home", icon:"fas fa-home"},
                {link: "About", icon:"fas fa-about"},
                {link: "Log In", icon:"fas fa-sign-in-alt"},
                {link: "Sign Up", icon:"fas fa-user-plus"},
            ],
            items: [
                {text: "Development", route: "#dev"},
                {text: "Design", route: "#des"},
                {text: "Marketing", route: "#mar"},
                {text: "Business", route: "#bus"}
            ]
        }
    },
    methods:{
      handleSCroll () {
        let header = document.querySelector(".v-toolbar");
        let menu = document.querySelector(".menu-button");
        let link = document.querySelector('.link')
        if (window.scrollY > 100 && !header.className.includes('v-toolbar--bgchange')) {
            header.classList.add('v-toolbar--bgchange'); 
            menu.classList.add('menu-button--colorchange');
            link.classList.add('link--colorchange');
        } else if (window.scrollY < 100) {
          header.classList.remove('v-toolbar--bgchange');
          menu.classList.remove('menu-button--colorchange');
          link.classList.remove('link--colorchange');
        }
      }
    },
    created () {
      window.addEventListener('scroll', this.handleSCroll);

    },
    destroyed () {
      window.removeEventListener('scroll', this.handleSCroll);
    } 
}
</script>

<style>
    .theme--light.v-toolbar--bgchange {
    background-color: #fff !important;
    color: #000 !important;
  }
  .theme--light.v-toolbar{
      background-color: transparent;
      color: #eee;
  }
  .menu-button{
      color: #fff !important;
  }
  .menu-button--colorchange{
      color: #000 !important;
  }
  .link{
      color: #fff !important;
  }
  .link--colorchange{
      color: #000 !important;
  }
  .v-list__tile.theme--light{
      padding: 0px;
  }
  .v-list__tile__title.nav{
      text-align: center !important;
  }
  i.fas.fa-sign-in-alt{
      color:#fff !important;
  }
  i.fas.fa-user-plus{
      color:#fff !important;
  }
  i.fas.fa-home{
      color:#fff !important;
  }
</style>
